@font-face {
  font-family: "digital-7";
  src: url("digital-7.ttf");
}

* {
  color: #03fe02;
  background-color: #000000;
  font-family: "digital-7", sans-serif;
  font-size: 30px;
  margin: 0;

  box-sizing: border-box;
}

.grid-container {
  margin: auto;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 30px 200px 40px;
  grid-gap: 20px;
  grid-row-gap: 10;
}
.grid-item {
  padding: 20px;
}
.item-1 {
  grid-column: 8/9;
  text-align: start;
}
.item-2 {
  grid-column: 5/8;
  text-align: right;
  font-size: 185px;
  display: inline-block;
  position: relative;
}
.item-3 {
  grid-column: 8 / 9;
  text-align: start;
  align-self: end;
}
.item-4 {
  grid-column: 4/7;
  text-align: end;
}
.item-5 {
  grid-column: 7/9;
  text-align: center;
}
